.loader {
  width: 64px;
  height: 64px;
  background-image: linear-gradient(#000 16px, #0000 0), linear-gradient(#ff3d00 16px, #0000 0), linear-gradient(#ff3d00 16px, #0000 0), linear-gradient(#000 16px, #0000 0);
  background-position: 0 0, 0 100%, 100% 0, 100% 100%;
  background-repeat: no-repeat;
  background-size: 16px 16px;
  animation: rotate 1s linear infinite;
  position: absolute;
  top: 55%;
  left: 48%;
}

@keyframes rotate {
  0% {
    width: 64px;
    height: 64px;
    transform: rotate(0deg);
  }

  50% {
    width: 30px;
    height: 30px;
    transform: rotate(180deg);
  }

  100% {
    width: 64px;
    height: 64px;
    transform: rotate(360deg);
  }
}

